.list-type5{
    width:600px;
    margin-left: 5px;
    }
    .list-type5 ol {
    list-style-type: none;
    margin: 0;
    margin-left: 1em;
    padding: 0;
    counter-reset: li-counter;
    }
    .list-type5 ol li{
    position: relative;
    margin-bottom: 1.5em;
    padding: 0.5em;
    background-color: white;
    padding-left: 58px;
    border: 1px solid #F0D756;
    border-radius: 5px;
    }
    
    .list-type5 a{
    text-decoration:none;
    color:black;
    font-size:15px;
    font-family: "Maven Pro", sans-serif;
    }
    
    .list-type5 li:hover{
    box-shadow:inset -1em 0 #6CD6CC;
    -webkit-transition: box-shadow 0.5s; /* For Safari 3.1 to 6.0 */
    transition: box-shadow 0.5s;
    }
    
    .list-type5 ol li:before {
    position: absolute;
    top: -0.3em;
    left: -0.5em;
    width: 1.8em;
    height: 1.2em;
    font-size: 2em;
    line-height: 1.2;
    font-weight: bold;
    text-align: center;
    color: white;
    background-color: #6CD6CC;
    transform: rotate(-20deg);
    -ms-transform: rotate(-20deg);
    -webkit-transform: rotate(-20deg);
    z-index: 99;
    overflow: hidden;
    content: counter(li-counter);
    counter-increment: li-counter;
    border-radius: 5px;
    }

/* Media query for small screens */
@media (max-width: 780px) {
    .list-type5{
        width:320px;
        margin-left: 5px;
    }
}